import Vue from 'vue';
let vue = new Vue();
const beforeClose = {
  before_close_normal: (resolve) => {
    vue.$confirm('', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }).then(() => {
      resolve(true)
    }).catch(() => {
      resolve(false)
    })
  }
}

export default beforeClose


